import { CloseCircleOutlined, SaveOutlined } from "@ant-design/icons";
import CApi from "@api";
import { CInput, CInputPassword, CTitle } from "@components";
import { FChange } from "@function";
import { Button, Card, Col, Form, Row, Typography } from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

const { Title } = Typography;
const Profile = () => {
  const router = useRouter();
  const [error, setError] = useState({});
  const [menuPath, setMenuPath] = useState("");
  const [userRole, setUserRole] = useState("");
  const [forms, setForms] = useState({
    id: "",
    name: "",
    email: "",
    password: "",
    password_confirmation: "",
  });

  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/", " / ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");

    let toCapitalize = toArray
      .map((word) => {
        return word[0].toUpperCase() + word.substring(1);
      })
      .join(" ");
    setMenuPath(toCapitalize);
    getMe();
  }, []);

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };

  const getMe = () => {
    CApi({
      method: "get",
      url: "api/me",
    }).then((res) => {
      console.log(res.data);
      setForms(res.data);
      setUserRole(res.data.role_name);
    });
  };

  return (
    <>
      <CTitle />
      <Card
        cover={
          <Col
            span={24}
            style={{
              backgroundColor: "#1890ff",
              paddingTop: "5px",
            }}
          />
        }
      >
        <Row
          style={{
            marginBottom: 25,
          }}
        >
          <Col span={24}>
            <Title level={3}>{menuPath}</Title>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: "center" }}>
            <Col span={24} style={{ textAlign: "center" }}>
              <Form {...formItemLayout}>
                <CInput
                  label="Name"
                  style={{ marginTop: -25 }}
                  placeholder="Name"
                  id="name"
                  value={forms.name}
                  validate={error.name}
                  onChange={(e) => FChange(e, forms, setForms)}
                />
                <CInput
                  label="Email"
                  style={{ marginTop: -25 }}
                  placeholder="Email"
                  id="email"
                  value={forms.email}
                  validate={error.email}
                  onChange={(e) => FChange(e, forms, setForms)}
                />
                <CInputPassword
                  label="Password"
                  style={{ marginTop: -25 }}
                  placeholder="Password"
                  id="password"
                  value={forms.password}
                  validate={error.password}
                  onChange={(e) => FChange(e, forms, setForms)}
                />
                <CInputPassword
                  label="Password Confirmation"
                  style={{ marginTop: -25 }}
                  placeholder="Password Confirmation"
                  id="password_confirmation"
                  value={forms.password_confirmation}
                  validate={error.password_confirmation}
                  onChange={(e) => FChange(e, forms, setForms)}
                />
                <CInput
                  label="Role"
                  style={{ marginTop: -25 }}
                  placeholder="Role"
                  id="role"
                  isDisabled
                  value={userRole}
                />
              </Form>
            </Col>
          </Col>
        </Row>
        <Row
          gutter={5}
          style={{
            float: "right",
          }}
        >
          <Col>
            <Button
              style={{
                backgroundColor: "#F0AD4E",
                color: "#fff",
                marginRight: 15,
              }}
              onClick={() => router.push("/")}
            >
              <CloseCircleOutlined />
              Cancel
            </Button>
            <Button
              style={{ backgroundColor: "#22BB33", color: "#fff" }}
              // onClick={() => handleSubmit()}
            >
              <SaveOutlined />
              Update
            </Button>
          </Col>
        </Row>
      </Card>
    </>
  );
};

export default Profile;
